import { type SnackbarCloseReason } from '@mui/material';
import { Action, type IAction } from '@strife/common';
import { injectable } from 'inversify';
import { type SyntheticEvent } from 'react';
import di from './di';
import { ToastsServiceId, type IToastData, type IToastHandle, type IToasts } from './toast.service.interface';

@injectable()
class Toasts implements IToasts {

  private _active?: IToastData;
  public get active(): IToastData | undefined {
    return this._active;
  }

  private _items = new Array<IToastData>();
  private _dataToHandle = new Map<IToastData, IToastHandle>();
  private _handleToData = new Map<IToastHandle, IToastData>();

  private _onChange: Action<[]> = new Action<[]>();
  public get onChange(): IAction<[]> {
    return this._onChange;
  }

  public add(data: IToastData): IToastHandle {
    const handle = new ToastHandle(this);
    this.initialize(data);
    this._items.push(data);
    this._dataToHandle.set(data, handle);
    this._handleToData.set(handle, data);

    if (!this._active) {
      this._active = this._items.shift();
      if (this._active)
        this._active.open = true;
    }

    this._onChange.invoke();
    return handle;
  }

  public remove(handle: IToastHandle): void {
    const data = this._handleToData.get(handle);
    if (!data)
      return;

    if (this._active == data) {
      if (this._active.onClose) {
        const event = new Event('Dismiss');
        const reason: SnackbarCloseReason = 'timeout';
        this._active.onClose(event, reason);
      }
      else
        this._active.open = false;
    } else {
      const index = this._items.indexOf(data);
      this._items.splice(index, 1);
    }

    this._active = this._items.shift();
    if (this._active)
      this._active.open = true;

    this._onChange.invoke();
  }

  private initialize(data: IToastData): void {
    data.open = false;
    const onClose = data.onClose;
    data.onClose = 
      (event: Event | SyntheticEvent<any, Event>, reason: SnackbarCloseReason): void => {
        if (onClose)
          onClose(event, reason);

        const handle = this._dataToHandle.get(data);
        handle?.dismiss();

        const index = this._items.indexOf(data);
        this._items.splice(index, 1);
      };
  }
  
}

class ToastHandle implements IToastHandle {
  
  private _toasts: IToasts;
  
  public constructor(toasts: IToasts) {
    this._toasts = toasts;
  }

  public dismiss(): void {
    this._toasts.remove(this);
  }

}

di.bind(ToastsServiceId).to(Toasts).inSingletonScope();